.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px;
  background-color: #fff;
  background-color: #262729;
  margin-bottom: 50px;
  box-shadow: 0px 2px 10px #34cad2;
  justify-content: flex-end;
}

.logo, .titulo {
  cursor: pointer;
}

.navbar-logo {
  display: flex;
  align-items: center;
  color: #34cad2;
  flex: 1;
}

.navbar-buttons ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  margin-right: 15px;
}

a {
  text-decoration: none;
}

.navbar-logo img {
  width: 50px;
  margin: 10px 15px 10px;
}

.navbar-buttons {
  margin-right: 20px;
}

.navbar-buttons a {
  margin-left: 10px;
  background-color: #262729;
  color: #34cad2;
  border: none;
  cursor: pointer;
}

.navbar-buttons a:hover {
  color: #fff;
  transition: 500ms;
}

@media (max-width: 640px) {
  .navbar-buttons {
    display: none;
  }

  .navbar-menu {
    display: block;
    cursor: pointer;
    margin-right: 30px;
  }

  .one,
  .two,
  .three {
    background-color: #34cad2;
    height: 5px;
    width: 100%;
    margin: 6px auto;
    transition-duration: 0.4s;
  }

  .menu-toggle {
    width: 40px;
    height: 30px;
    margin: 20px 30px
  }

  .menu-section.on {
    position: absolute;
    top:0;
    right:0;
    width: 80%;
    height: 80%;
    background-color: #262729;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0px 0px 10px 20px;
    box-shadow: 0px 2px 10px #34cad2;
  }

  .menu-section.on .navbar-buttons {
    display: block;
  }

  .menu-section.on .menu-toggle {
    position: absolute;
    right: 0;
    top: -4px;
  }

  .menu-section.on .menu-toggle .one {
    transform: rotate(45deg) translate(7px, 7px);
  }

  .menu-section.on .menu-toggle .two {
    opacity: 0;
  } 

  .menu-section.on .menu-toggle .three {
   transform: rotate(-45deg) translate(8px, -9px);
  } 

  .menu-section.on div ul {
    text-align: center;
    display: block;
    padding: 0;
    margin: 0;
  }

  .menu-section.on div ul a{
    transition-duration: 0.5s;
    font-size: 2rem;
    line-height: 3rem;
    display: block;
  }

  .navbar-buttons {
    margin: 0;
  }

}

@media (max-width: 350px) {
  .navbar-logo {
    font-size: 0.8rem;
  }

  .menu-toggle {
    margin: 0 10px;
    width: 35px;
  }

  .navbar-logo img {
    width: 38px;
    margin: 10px 15px 10px;
  }
}

@media (max-width: 281px) {
  .navbar {
    width: 285px;
  }
}